@import url("https://fonts.googleapis.com/css?family=Exo+2:400");

@font-face {
  font-family: "MyriadPro-Semibold";
  font-style: normal;
  font-weight: 400;
  src: url("https://anima-uploads.s3.amazonaws.com/5b530adf29760f00097e1a6e/Myriad-Pro-Semibold_31650.ttf")
    format("truetype");
}

@font-face {
  font-family: "MyriadPro-Regular";
  font-style: normal;
  font-weight: 400;
  src: url("https://anima-uploads.s3.amazonaws.com/5b530adf29760f00097e1a6e/Myriad-Pro_31655.ttf")
    format("truetype");
}

@font-face {
  font-family: "KozGoPr6N-Regular";
  font-style: normal;
  font-weight: 400;
  src: url("https://anima-uploads.s3.amazonaws.com/5c45557aa2a5d20009492e53/KozGoPr6N-Regular.otf")
    format("opentype");
}

@font-face {
  font-family: "HelveticaNeue-Regular";
  font-style: normal;
  font-weight: 400;
  src: url("https://anima-uploads.s3.amazonaws.com/5cf8ce54520dfb8030e86611/HelveticaNeue-Regular.ttf")
    format("truetype");
}

.component-wrapper a,
.screen a {
  text-decoration: none;
  display: contents;
}

.full-width-a {
  width: 100%;
}

.full-height-a {
  height: 100%;
}

.screen textarea:focus,
.screen input:focus {
  outline: none;
}

.screen *,
.component-wrapper * {
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
}

.screen div {
  -webkit-text-size-adjust: none;
}

.container-center-vertical,
.container-center-horizontal {
  pointer-events: none;
  display: flex;
  flex-direction: row;
  padding: 0;
  margin: 0;
}

.container-center-vertical {
  align-items: center;
  height: 100%;
}

.container-center-horizontal {
  justify-content: center;
  width: 100%;
}

.container-center-vertical > *,
.container-center-horizontal > * {
  pointer-events: auto;
  flex-shrink: 0;
}

.component-wrapper,
.screen {
  overflow-wrap: break-word;
  word-wrap: break-word;
  word-break: break-all;
  word-break: break-word;
}

.auto-animated div {
  opacity: 0;
  position: absolute;
  --z-index: -1;
}

.auto-animated .container-center-vertical,
.auto-animated .container-center-horizontal {
  opacity: 1;
}

.overlay {
  position: absolute;
  opacity: 0;
  display: none;
  top: 0;
  width: 100%;
  height: 100%;
  position: fixed;
}

.animate-appear {
  opacity: 0;
  display: block;
  animation: reveal 0.3s ease-in-out 1 normal forwards;
}

.animate-disappear {
  opacity: 1;
  display: block;
  animation: reveal 0.3s ease-in-out 1 reverse forwards;
}

.animate-nodelay {
  animation-delay: 0s;
}

@keyframes reveal {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.align-self-flex-start {
  align-self: flex-start;
}
.align-self-flex-end {
  align-self: flex-end;
}
.align-self-flex-center {
  align-self: center;
}
.valign-text-middle {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.valign-text-bottom {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}
input:focus {
  outline: none;
}
.component-wrapper,
.component-wrapper * {
  pointer-events: none;
}

.component-wrapper a *,
.component-wrapper a,
.component-wrapper input,
.component-wrapper video,
.component-wrapper iframe,
.listeners-active,
.listeners-active * {
  pointer-events: auto;
}

.hidden,
.hidden * {
  visibility: hidden;
  pointer-events: none;
}

.smart-layers-pointers,
.smart-layers-pointers * {
  pointer-events: auto;
  visibility: visible;
}

.component-wrapper.not-ready,
.component-wrapper.not-ready * {
  visibility: hidden !important;
}

.listeners-active-click,
.listeners-active-click * {
  cursor: pointer;
}