.loader:empty {
  position: absolute;
  top: calc(50% - 1.1em);
  left: calc(50% - 1.1em);
  width: 3em;
  height: 3em;
  border: 0.3em solid rgba(0, 0, 0, 0.2);
  border-left: 0.3em solid  #66cc33;
  animation: load8 1.1s infinite linear;
}

@keyframes load8 {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.center {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}


