.navbar {
  transition: all 0.4s;
  margin-left: auto;
  margin-right: auto;
position:absolute;
  padding-left: 22px;
  padding-right: 22px;
  height: 30px;

  background: rgba(39, 39, 39, 0.3);
  box-shadow: 1px 2px 10px rgba(0, 0, 0, 0.1);

  margin-bottom: none;
  
}

body {
  padding-bottom: 20px;
}

.navbar {
  margin-bottom: 20px;
}


.banner {
  transition: all 0.4s;
  margin-left: auto;
  margin-right: auto;
  height: 30px;

  margin-top: 33px;

  margin-bottom: none;
  background: rgba(39, 39, 39, 1);
}

.navbar2 {
  transition: all 0.4s;
  margin-left: auto;
  margin-right: auto;
  padding-left: 22px;
  padding-right: 22px;
  height: 30px;

  background: rgba(39, 39, 39, 1);
}

 .dropdown-menu>li>a, .show .dropdown-menu {
  background:rgba(0, 0, 0, 1);
}
.dropdown-item>li>a, .show .dropdown-item:hover,
.dropdown-item>li>a, .show .dropdown-item:focus{
  background-color:transparent;
  ;
}
li{
  background-color:transparent;
  
}

.dropdown-item{
  background:transparent;
  ;
}

.basic-navbar-nav{
  
  background-color: rgba(0, 0, 0, 0.7);
  box-shadow: 1px 2px 10px rgba(0, 0, 0, 0.1);
}

 .nav-link
 {
  color: 'transparent';
  
}

.nav-item .nav-link:hover,
.nav-item .nav-link:focus {
  text-decoration: underline;
  
}




.navbar-brand {
  color: #fff;
}


/* Change navbar styling on scroll */
.navbar.active {
  box-shadow: 1px 2px 10px rgba(0, 0, 0, 0.1);
}

.navbar.active .nav-link {
  color: #66cc33;
}

.navbar.active .nav-link:hover,
.navbar.active .nav-link:focus {
  color: #66cc33;
  text-decoration: none;
}




.nav-link {
  display: block;
  padding: 7px 0px;
  text-align: center;
  color: #fff;
}
.nav-item:hover {
  text-decoration:wavy;
}

.nav-link:hover,
.nav-link:focus {
  text-decoration: wavy;
}

.hover-item {
	color: #66cc33;
}

.hover-item:hover {
	background-color: inherit;
}



@media screen and (max-width: 991px) {

    .navbar {
      background: rgba(0, 0, 0, 0.3);
      height: auto;
    }

    .banner {
      transition: all 0.4s;
      margin-left: auto;
      margin-right: auto;
      height: 24px;
    display:flex;
      margin-top: 66px;
      background: rgba(39, 39, 39, 1);
    }
  
    .navbar .navbar-brand, .navbar .nav-link {
      color: #fff;
    }
  }
  
  